import axios from "axios";
import Vue from "vue";
import {vm} from '../main.js'
import router from "./router.js"
import store from './vuex.js';
import * as types from '../store/types.js'

// //
axios.interceptors.request.use(function(config) {
//   //config   axios(config)
  
  // console.log(config)
//   // token
  let token = window.localStorage.getItem('user')
  token = token ? JSON.parse(window.localStorage.getItem('user')).token : '',
  // console.log(token)
  config.headers={
    token: token
  }
  
//   //loading
// console.log(vm)
store.commit(types.UPDATE_ISLOADING,true)
  // vm.isLoading = true;
  
  // 
  return config;
}, function(error) {
  // 
  return Promise.reject(error);
});

// // 
axios.interceptors.response.use(function(response) {
  //response  ~~ axiosres
  //    res.status   res.data.err == 2
  //tokenlogin,
  //string
  let currentRoute = router.currentRoute.fullPath;
  // console.log(router)
  // console.log(router.currentRoute)
  
  if(response.data.err===2 && !currentRoute.includes('/Login')){
    
    router.replace({
      path:'/Login',
      query:{p:currentRoute}
    })
  }
  
  // vm.isLoading = false;
  store.commit(types.UPDATE_ISLOADING,false)
  
  return response;//
}, function(error) {
  // 
  return Promise.reject(error);
});


Vue.prototype.$axios=axios;
// // window.axios=axios;  
export default axios;